              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="货位" size="mini">
            <el-select clearable class="el_slect" v-model="params.allocation_id" placeholder="请选择" @change="allocationChange" @focus="InventoryInvok" size="mini">
              <el-option :label="item.allocation_name" :value="item.allocation_id" :key="item.allocation_id" v-for="item in allocationInventoryList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" maxlength="100" placeholder="请输入" @keyup.enter.native="onSearch" clearable></el-input>
          </el-form-item>
          <el-form-item label="商品编号" size="mini">
            <el-input v-model="params.goods_id" maxlength="100" placeholder="请输入" @keyup.enter.native="onSearch" clearable></el-input>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" @change="departmentChange" filterable maxlength="100">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select clearable v-model="params.auditor_status">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="采购员" size="mini">
            <erpEmployee v-model="params.buyer_id" title='请选择采购员' :searchParams="{ company_id: params.company_id }" />
          </el-form-item>
          <el-form-item label="分摊方式" size="mini">
            <el-select clearable v-model="params.other_price_type" placeholder="请选择">
              <el-option label="不分摊" :value="0">不分摊</el-option>
              <el-option label="按金额分摊" :value="1">按金额分摊</el-option>
              <el-option label="按数量分摊" :value="2">按数量分摊</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="付款方式" size="mini">
            <el-select clearable v-model="params.receivables_mode_id" placeholder="请选择" @focus="receivablesInvok" @change="receivablesChange">
              <el-option :label="item.receivables_mode_name" :value="item.receivables_mode_id" :key="item.receivables_mode_id" v-for="item in receivablesList "></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="工厂代码" size="mini">
            <el-input v-model="params.factory_code" placeholder="请输入" @keyup.enter.native="onSearch" clearable></el-input>
          </el-form-item>
          <el-form-item label="厂家发货单号" size="mini">
            <el-input v-model="params.factorysend_sn" maxlength="100" placeholder="请输入" @keyup.enter.native="onSearch" clearable></el-input>
          </el-form-item>
          <el-form-item label="厂家发货日期" size="mini">
            <el-date-picker v-model="params.factorysend_time" type="date" placeholder="请选择" value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="timeValue1" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
            </el-date-picker>
          </el-form-item>
          <el-form-item label="供货方式" size="mini">
            <el-select filterable clearable v-model="params.supply_mode" placeholder="请选择">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="现货" :value="1"></el-option>
              <el-option label="非现货" :value="2"></el-option>
              <el-option label="其他" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="入库来源" size="mini">
            <el-select clearable v-model="params.instorage_source">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="ERP" :value="1">ERP</el-option>
              <el-option label="销售订单" :value="3">销售订单</el-option>
              <el-option label="集采平台" :value="2">集采平台</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="助记码" size="mini">
            <el-input v-model="params.logogram" maxlength="50" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-dropdown placement="bottom-start" v-if="authority.b_export==1">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu name="testflag" slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item name="testflag1" @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item name="testflag2" @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="table_container">
        <xTable :ajax='{ url: "getInStorageDetailList", params: params }' fieldUrl='v2/purchase/InStorageDetail/getInStorageDetailList' ref='xTable' @getData='getTableData' :footerMethod='footerMethod' :tableColumn='tableColumn' :xData='tableData' />
      </div>
      <div class="table_info">
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <div>生成时间：{{usertIME.time }}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr">制表人：{{usertIME.user }}</div>
          </el-col>
        </el-row>
      </div>
    </div>

  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { opthionList } from "common/utils/public_function/commonUtils";
import { TableHead } from "./instorageIndex.js";
export default {
  name: "purchaseInstorageIndex",
  mixins: [TableHead],
  data() {
    return {
      hasAuditorStatusPrint: null,
      dialogEmployeeImportVisible: false,
      purchaseprice: true, //int 初始化显示数据列表展示
      planAdds: false, //增加 修改采购计划列表
      examineprice: false, //查看采购计划详情
      lock: 0,
      timeValue1: [],
      params: {},
      usertIME: {
        user: localStorage.getItem("realname"),
        time:
          this.$publicFution.formatData(new Date()) +
          `  ${new Date().getHours()}:${new Date().getMinutes()}:${new Date().getSeconds()}`,
      },
      AccordingParams: {}, //参数传递值
      printFlag: true,
      timeValue: [],
      multipleSelection: [], //已选项
      dialogFormVisible: false,
      newresult: [],
    };
  },
  computed: {
    ...mapGetters(["instorageMutationsGet"]),
  },
  methods: {
    ...mapActions(["InStorageDetailIndexACT"]),
    receivablesChange(val) {
      if (val) {
        this.params.receivables_mode_name = this.receivablesList.filter(
          (obj) => obj.receivables_mode_id == val
        )[0].receivables_mode_name;
      }
    },
    allocationChange(val) {
      if (val) {
        this.params.allocation_name = this.allocationInventoryList.filter(
          (obj) => obj.allocation_id == val
        )[0].allocation_name;
      }
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let s = Object.assign(this.params, {
        export_type: scheduleType,
      });
      this.$axMethod.exportInStorageDetailUrl(this.params).then((res) => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    downLoadFile() {
      this.$axMethod.purchaseDownloadFile({}).then((response) => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          (obj) => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "supplier_id", "");
      this.$set(this.params, "department_id", "");
      this.$set(this.params, "warehouse_id", "");
      this.$set(this.params, "buyer_id", "");
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          (obj) => obj.department_id === val
        )[0].department_name;
      }
      this.$set(this.params, "buyer_id", "");
    },
    buyerChange(val) {
      if (val) {
        this.params.buyer_name = this.employeeList.filter(
          (obj) => obj.employee_id === val
        )[0].employee_name;
      }
    },
    warehouseChange(val) {
      if (val) {
        this.params.warehouse_name = this.warehouseList.filter(
          (obj) => obj.warehouse_id === val
        )[0].warehouse_name;
      }
    },
    supplierChangesave(val) {
      if (val) {
        this.params.supplier_name = this.supplierList.filter(
          (obj) => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.timeValue = [];
      this.timeValue1 = [];
      this.departmentList = [];
      this.$set(this, "params", {
        auditor_status: 0,
        supply_mode: 0,
      });
      this.onSearch();
    },
    onSearch() {
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.start_auditor_time = this.timeValue1[0];
        this.params.end_auditor_time = this.timeValue1[1];
      } else {
        this.params.start_auditor_time = this.params.end_auditor_time = "";
      }
      this.$refs.xTable._getData(1);
    },
  },
  mounted() {
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
    this.authority = JSON.parse(localStorage.getItem("authority"));
    if (this.instorageMutationsGet && this.instorageMutationsGet.params) {
      this.$set(this, "params", this.instorageMutationsGet.params);
      if (this.instorageMutationsGet.params.start_time) {
        const { start_time, end_time } = this.instorageMutationsGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      Object.assign(this.instorageMutationsGet.params, {
        employee_id: this.instorageMutationsGet.params.buyer_id,
        employee_name: this.instorageMutationsGet.params.buyer_name,
      });
      opthionList(this, this.instorageMutationsGet.params);
    } else {
      this.$set(this, "params", {
        auditor_status: 0,
        supply_mode: 0,
      });
    }
    this.onSearch();
  },
};
</script>

